.App {
  text-align: center;
  margin-top: 50px;
  font-family: "Segoe UI", sans-serif;
}

input {
  padding: 10px;
  margin-right: 10px;
  width: 200px;
}

button {
  padding: 10px 20px;
  background-color: #0077ff;
  border: none;
  color: white;
  cursor: pointer;
  border-radius: 5px;
}

ul {
  margin-top: 30px;
  list-style: none;
  padding: 0;
}

li {
  font-size: 18px;
  padding: 8px;
}
